

.sms-fax-wrap {
	@media(min-width: 992px) {
		background-image: url('../images/sms-fax-pattern.webp');
		background-position: left top 120px;
		background-repeat: no-repeat;
		background-size: contain;
	}
	.scheduling{
		&:after ,
		&:before{
			display: none;
		}
	}
}
.security-sec {
	.container {
		row-gap: 25px;
	}
	.security-left{
		
		@media(min-width: 992px) {
			padding-right: 60px;
		}
	}
	.contact-col {
		.icon{
			width: 64px;
			font-size: 0;
			@media(max-width: 575px) { 
				width: 50px;
			}
		}
		.details{
			width: calc(100% - 64px);
			padding-left: 48px;
			@media(max-width: 767px) {
				padding-left:35px;
			}
			@media(max-width: 575px) {
				padding-left:15px;
				width: calc(100% - 50px);
			}
		}
		+ .contact-col {
			padding-top: 75px;
			@media(max-width: 767px) {
				padding-top: 60px;
			}
			@media(max-width: 575px) {
				padding-top: 45px;
			}
		}
	}
} 
.review-sec {
	position: relative;
	z-index: 1;
	.review-pattern{
		position: absolute;
		z-index: -1;
		left: 47%;
		top: -56px;
		transform: translateX(-50%);
		width: 100%;
		height: 100%;
		text-align: center;
		img{
			max-width: 90%;
		}
	}
	.text-with-bgin{
		max-width: 1200px;
		margin: 0 auto;
		@media(max-width: 575px) {
			text-align:center
		}
	}
	.reviewtxt{
		width: calc(100% - 360px);
		@media(max-width: 1439px) {
			width: calc(100% - 300px);
		}
		@media(max-width: 1199px) {
			width: calc(100% - 220px);
		}
		@media(max-width: 767px) {
			width: 100%;
			order:1; padding-top :20px;
		}
		h6{
			margin-top: 50px;
			@media(max-width: 767px) {
				margin-top: 30px;
			}
			span {
				padding-top: 5px;
				display: block;
				font-weight: 400;
				font-size: $px16;
			}
		}

	}
	.review-logo{
		width: 186px;
		height: 186px;
		@media(max-width: 767px) {
			width:120px; height:120px; margin: 0 auto;
			img{
				max-width: 70%;
			}
		}
		@media(max-width: 575px) {
			width:100px; height:100px; 
		}
	}
}



.ptnt-mgmt{
	.wrap{
		padding: 0; background-color: transparent;
	} 
	.sec-workflow {
		.cust-workflow {
			.section-nav{
				max-width: 650px;
				ul {
					li{
						padding-left: 50px;
					}
				}
			}
		}	
	}
	&.workflow {
		.cust-workflow {
			&.col-right {
				height: auto;
			}
			 .target-div {
			 	top: 50%;
			 	width: 100%;
			 	transform: translateY(-50%);
			 	text-align: right;
			 }
		}
	}
}

.cust-tabs{
	.cloud-list-wrap{
		margin-bottom: 0;
		li {
			background-color: rgba(255,255,255,.05);
			color: #fff;
		}
		&.without-bg {
			li {
				background-color: transparent;
				padding: 0;
				width: 100%;
			}
		}
	}
}
.cust-tabs-t2 {
	.tabs__nav{
		margin: 0 !important;
		order: 1;
		    width: 100%;
    justify-content: flex-end;

	}
}